header {
    width: 100%;
    padding: 1rem;
    box-shadow: 0 0 8px gray;
    transition-duration: 0.6s;

    &.dark {
        background: black;
        color: white;
    }


    nav {
        max-width: 1280px;
        margin: auto;

        display: flex;
        justify-content: space-between;

        ul:nth-child(1) {
            list-style: none;
            align-items: center;
            display: flex;
            gap: 1rem;

            li {

                p {
                    width: 280px;
                    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                    font-size: 18px;
                    font-weight: bolder;
                }
            }
        }

        ul:nth-child(2) {
            list-style: none;
            align-items: center;
            display: flex;
            gap: 4rem;

            li {
                a {
                    text-decoration: none;
                    color: black;
                    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                    font-size: 16px;
                    font-weight: bold;
                    transition-duration: 0.6s;

                    &.dark {
                        color: white;
                    }
                }
            }
        }

        ul:nth-child(3) {
            display: flex;
            list-style: none;
            align-items: center;

            a {
                color: black;
                text-decoration: none;
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                font-size: 16px;
                font-weight: bold;
            }

            select {
                margin: 0 15px;
                border: none;
                outline: none;
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                font-size: 20px;
                transition-duration: 0.6s;

                &.dark {
                    color: white;
                    background-color: black;
                }
            }

            button {
                width: 100px;
                height: 40px;
                border: none;
                background-color: blue;
                color: white;
                border-radius: 5px;
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                font-size: 18px;
            }
        }

        ul:nth-child(4) {
            display: none;
            list-style: none;
            align-items: center;
        }
    }

    .hamburger {
        display: none;

        &.active {
            display: block;

            @media(min-width: 965px){
                display: none;
            }
        }
    }

    .hamburger {

        .hamburger_wrapper {
            ul:nth-child(1) {
                list-style: none;
                align-items: center;
                gap: 4rem;

                li {
                    padding-bottom: 10px;
                    a {
                        text-decoration: none;
                        color: black;
                        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                        font-size: 16px;
                        font-weight: bold;
                        transition-duration: 0.6s;

                        &.dark {
                            color: white;
                        }
                    }
                }
            }

            ul:nth-child(2) {
                display: flex;
                list-style: none;
                align-items: center;

                a {
                    color: black;
                    text-decoration: none;
                    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                    font-size: 16px;
                    font-weight: bold;
                }

                select {
                    margin: 0 15px;
                    border: none;
                    outline: none;
                    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                    font-size: 20px;
                    transition-duration: 0.6s;

                    &.dark {
                        color: white;
                        background-color: black;
                    }
                }

                button {
                    width: 100px;
                    height: 40px;
                    border: none;
                    background-color: blue;
                    color: white;
                    border-radius: 5px;
                    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                    font-size: 18px;
                }
            }
        }
    }

}

@media only screen and (max-width: 965px) {
    header {
        nav {
            ul:nth-child(4) {
                display: flex;
            }

            ul:nth-child(2) {
                display: none;
            }

            ul:nth-child(3) {
                display: none;
            }
        }
    }
}

@media only screen and (max-width: 405px) {
    header {
        nav {
            ul:nth-child(1) {
                li {
                    p {
                        width: 200px;
                    }
                }
            }

        }
    }
}