.login {
    width: 100%;
    

    .login_wrapper{
        width: 700px;
        margin: auto;
        margin-top: 100px;
        border-radius: 10px;
        box-shadow:  0 0 8px gray;

        .login_title{
            width: 100%;

            h1{
                text-align: center;
                padding: 20px 0 50px 0;
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                font-size: 45px;
            }
        }

        form{
            width: 100%;

            p{
                padding: 20px 0 5px 5px;
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                font-size: 22px;

                span{
                    font-size: 18px;
                    color: red;
                }
            }

            input{
                width: 400px;
                height: 45px;
                padding-left: 5px;
                margin-left: 10px;
                border: none;
                outline: none;
                border-bottom: 1px solid gray;
                border-radius: 10px;
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                font-size: 22px;
            }

            button{
                width: 400px;
                height: 45px;
                margin: 50px 0 50px 10px;
                border: none;
                outline: none;
                border-radius: 10px;
                color: white;
                background-color: rgb(94, 94, 244);
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                font-size: 25px;
                letter-spacing: 10px;
            }
        }
    }
}