.card{
    width: 24%;
    box-shadow: 0 0 8px gray;
    padding: 1rem;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    cursor: pointer;
    transition-duration: 0.6s;

    &.dark{
        color: white;
    }

    img{
        width: 100%;
        height: 300px;
    }

    &:hover{
        opacity: 50%;
    }

   
}

@media only screen and (max-width: 1278px){
    .card{
        width: 33%;

        h3{
            font-size: 10px;
        }
    }
}