section {
    width: 100%;
    padding: 40px;
    transition-duration: 0.6s;

    &.dark {
        background: black;
        color: white;
    }

    .section2_wrapper {
        max-width: 700px;
        margin: auto;

        form {
            width: 100%;
            text-align: left;

            .selects {
                width: 100%;
                display: flex;
                justify-content: space-between;

                select {
                    width: 48%;
                    height: 40px;
                    padding: 2px 5px;
                    margin-bottom: 15px;
                    border-radius: 500px;
                    transition-duration: 0.6s;

                    &.dark {
                        background: black;
                        color: white;
                    }


                    &:focus {
                        outline: 1px solid blue;
                        box-shadow: 0 0 10px rgb(67, 67, 243);
                    }
                }
            }

            .google_map {
                width: 100%;
                box-shadow: 0 0 10px gray;
                border-radius: 20px;
            }

            p {
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                font-size: 22px;
                padding: 20px 0 3px 0;
            }

            input {
                width: 50%;
                height: 40px;
                border-radius: 10px;
                border: none;
                border-bottom: 1px solid gray;
                padding-left: 8px;
                outline: none;
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                font-size: 22px;
                transition-duration: 0.6s;

                &.dark {
                    background: black;
                    color: white;
                }
            }

            button {
                width: 55%;
                height: 40px;
                margin: 20px 0;
                border-radius: 10px;
                color: white;
                background-color: blue;
                border: none;
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                font-size: 22px;


            }
        }
    }
}