main {
    width: 100%;
    padding: 20px 0;
    background: rgb(206, 213, 227);

    .main_wrapper {
        max-width: 1280px;
        margin: auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        img {
            width: 30%;
        }
    }
}

@media only screen and (max-width: 1315px) {
    main {
        .main_wrapper {
            gap: 2rem;
            justify-content: center;
            img {
                width: 45%;
            }
        }
    }
}

@media only screen and (max-width: 1284px) {
    main {
        .main_wrapper {
            gap: 2rem;
            justify-content: center;
            img {
                width: 90%;
            }
        }
    }
}

