.card__wrapper {
    max-width: 1280px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
    padding-bottom: 2rem;

    @media only screen and (max-width: 1278px) {
        .card__wrapper {
            justify-content: space-between;
            flex-wrap: wrap;
        
        }
    }
}

