section{
    width: 100%;
    transition-duration: 0.6s;

    &.dark{
        background: black;
    }

    .section_wrapper{
        max-width: 1280px;
        margin: auto;
        

        .title{
            width: 100%;
            display: flex;
            gap: 2rem;

            h1:nth-child(1){
                color: rgb(78, 78, 175);
                margin: 30px 0 10px 0;
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                font-size: 30px;
                font-weight: bolder;
            }

            h1:nth-child(2){
                color: gray;
                margin: 30px 0 10px 0;
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                font-size: 30px;
                font-weight: bolder;
            }
        }

        .line{
            width: 250px;
            border: 1px solid rgb(78, 78, 175);
        }

        .line_1{
            width: 100%;
            margin: 20px 0;
            border: 0.5px solid rgb(185, 180, 180);
        }
    }
}